// __tests__/components/exercise-common/CategorySelector.test.js
import React from "react";
import { render, waitFor } from "@testing-library/react-native";
import CategorySelector from "../../../src/components/exercise-common/CategorySelector";

// üîß Mock Animated API
const mockTiming = jest.fn(() => ({ start: jest.fn(cb => cb && cb()) }));
const mockParallel = jest.fn(() => ({ start: jest.fn(cb => cb && cb()) }));

jest.mock("react-native", () => {
  const RN = jest.requireActual("react-native");
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: jest.fn(() => ({ interpolate: jest.fn() })),
      timing: mockTiming,
      parallel: mockParallel,
      multiply: jest.fn((a, b) => a || b),
    },
  };
});

describe("CategorySelector - Animations", () => {
  const categories = [
    { id: "1", name: "Category 1" },
    { id: "2", name: "Category 2" },
  ];

  beforeEach(() => {
    jest.clearAllMocks();
  });

  it("triggers parallel animations when selectedCategory changes", async () => {
    const { rerender } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={jest.fn()}
      />
    );

    // attendre l'init des Animated.Value
    await waitFor(() => {
      expect(require("react-native").Animated.Value).toHaveBeenCalled();
    });

    // changer la s√©lection
    rerender(
      <CategorySelector
        categories={categories}
        selectedCategory="2"
        onSelectCategory={jest.fn()}
      />
    );

    // attendre que les animations soient appel√©es
    await waitFor(() => {
      expect(mockParallel).toHaveBeenCalledTimes(1);
      expect(mockTiming).toHaveBeenCalledTimes(2);
    });
  });

  it("prevents animation conflicts with isAnimatingRef", async () => {
    const { rerender } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={jest.fn()}
      />
    );

    await waitFor(() => {
      expect(require("react-native").Animated.Value).toHaveBeenCalled();
    });

    // Premi√®re transition ‚Üí doit lancer une animation
    rerender(
      <CategorySelector
        categories={categories}
        selectedCategory="2"
        onSelectCategory={jest.fn()}
      />
    );

    await waitFor(() => {
      expect(mockParallel).toHaveBeenCalledTimes(1);
    });

    // Deuxi√®me transition imm√©diate ‚Üí bloqu√©e par isAnimatingRef
    rerender(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={jest.fn()}
      />
    );

    // mockParallel ne doit pas avoir augment√©
    await waitFor(() => {
      expect(mockParallel).toHaveBeenCalledTimes(1);
    });
  });
});
